var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"wrapper"},[_c('div',{staticClass:"back",on:{"touchstart":_vm.back}},[_vm._v("返回")]),_vm._v(" "),_c('div',{staticClass:"term"},[_c('scroll',{staticClass:"term_content",attrs:{"probeType":_vm.probeType}},[_c('div',{staticClass:"term_text"},[_c('h3',{staticClass:"term_title"},[_vm._v("腾讯服务协议")]),_vm._v("\n        欢迎您使用腾讯的服务!"),_c('br'),_vm._v("为使用腾讯的服务，您应当阅读并遵守《腾讯服务协议》（以下简称“本协议”）和《QQ号码规则》。请您务必审慎阅读、充分理解各条款内容，特别是免除或者限制责任的条款、管辖与法律适用条款，以及开通或使用某项服务的单独协议。限制、免责条款可能以黑体加粗或加下划线的形式提示您重点注意。除非您已阅读并接受本协议所有条款，否则您无权使用腾讯提供的服务。您使用腾讯的服务即视为您已阅读并同意上述协议的约束。"),_c('br'),_vm._v(" 如果您未满18周岁，请在法定监护人的陪同下阅读本协议，并特别注意未成年人使用条款。"),_c('br'),_vm._v("一、【协议的范围】"),_c('br'),_vm._v("1.1本协议是您与腾讯之间关于用户使用腾讯相关服务所订立的协议。“腾讯”是指腾讯公司及其相关服务可能存在的运营关联单位。“用户”是指使用腾讯相关服务的使用人，在本协议中更多地称为“您”。\n        　　"),_c('br'),_vm._v("1.2本协议项下的服务是指腾讯向用户提供的包括但不限于即时通讯、网络媒体、互联网增值、互动娱乐、电子商务和广告等产品及服务（以下简称“本服务”）。"),_c('br'),_vm._v("1.3本协议内容同时包括《QQ号码规则》（链接地址： http://zc.qq.com/chs/agreement1_chs.html）和《隐私政策》（链接地址：\n        http://www.qq.com/privacy.htm）,且您在使用腾讯某一特定服务时，该服务可能会另有单独的协议、相关业务规则等（以下统称为“单独协议”）。上述内容一经正式发布，即为本协议不可分割的组成部分，您同样应当遵守。您对前述任何业务规则、单独协议的接受，即视为您对本协议全部的接受。"),_c('br'),_vm._v("二、【帐号与密码安全】 "),_c('br'),_vm._v("2.1您在使用腾讯的服务时可能需要注册一个帐号。关于您使用帐号的具体规则，请遵守《QQ号码规则》和单独协议。"),_c('br'),_vm._v("2.2腾讯特别提醒您应妥善保管您的帐号和密码。当您使用完毕后，应安全退出。因您保管不善可能导致遭受盗号或密码失窃，责任由您自行承担。"),_c('br'),_vm._v("三、【用户个人信息保护】 　　3.1保护用户个人信息是腾讯的一项基本原则。腾讯将按照本协议及《隐私政策》（链接地址：http://www.qq.com/privacy.htm）的规定收集、使用、储存和分享您的个人信息。本协议对个人信息保护规定的内容与上述《隐私政策》有相冲突的，及本协议对个人信息保护相关内容未作明确规定的，均应以《隐私政策》的内容为准。"),_c('br'),_vm._v("3.2您在注册帐号或使用本服务的过程中，可能需要填写一些必要的信息。若国家法律法规有特殊规定的，您需要填写真实的身份信息。若您填写的信息不完整，则无法使用本服务或在使用过程中受到限制。"),_c('br'),_vm._v("3.3一般情况下，您可随时浏览、修改自己提交的信息，但出于安全性和身份识别（如号码申诉服务）的考虑，您可能无法修改注册时提供的初始注册信息及其他验证信息。"),_c('br'),_vm._v("3.4腾讯将运用各种安全技术和程序建立完善的管理制度来保护您的个人信息，以免遭受未经授权的访问、使用或披露。 　　3.5腾讯不会将您的个人信息转移或披露给任何非关联的第三方，除非："),_c('br'),_vm._v("（1）相关法律法规或法院、政府机关要求；"),_c('br'),_vm._v("（2）为完成合并、分立、收购或资产转让而转移；或 "),_c('br'),_vm._v("（3）为提供您要求的服务所必需。"),_c('br'),_vm._v("3.6腾讯非常重视对未成年人个人信息的保护。若您是18周岁以下的未成年人，在使用腾讯的服务前，应事先取得您家长或法定监护人（以下简称\"监护人\"）的书面同意。"),_c('br'),_vm._v("\n        四、【使用本服务的方式】 "),_c('br'),_vm._v("4.1除非您与腾讯另有约定，您同意本服务仅为您个人非商业性质的使用。 "),_c('br'),_vm._v("4.2您应当通过腾讯提供或认可的方式使用本服务。您依本协议条款所取得的权利不可转让。 "),_c('br'),_vm._v("4.3您不得使用未经腾讯授权的插件、外挂或第三方工具对本协议项下的服务进行干扰、破坏、修改或施加其他影响。"),_c('br'),_vm._v("\n        五、【按现状提供服务】 您理解并同意，腾讯的服务是按照现有技术和条件所能达到的现状提供的。腾讯会尽最大努力向您提供服务，确保服务的连贯性和安全性；但腾讯不能随时预见和防范法律、技术以及其他风险，包括但不限于不可抗力、病毒、木马、黑客攻击、系统不稳定、第三方服务瑕疵、政府行为等原因可能导致的服务中断、数据丢失以及其他的损失和风险。\n        "),_c('br'),_vm._v("六、【自备设备】"),_c('br'),_vm._v("6.1您应当理解，您使用腾讯的服务需自行准备与相关服务有关的终端设备（如电脑、调制解调器等装置），并承担所需的费用（如电话费、上网费等费用）。 　　6.2您理解并同意，您使用本服务时会耗用您的终端设备和带宽等资源。\n        "),_c('br'),_vm._v("七、【广告】"),_c('br'),_vm._v("7.1您同意腾讯可以在提供服务的过程中自行或由第三方广告商向您发送广告、推广或宣传信息（包括商业与非商业信息），其方式和范围可不经向您特别通知而变更。 　　7.2腾讯可能为您提供选择关闭广告信息的功能，但任何时候您都不得以本协议未明确约定或腾讯未书面许可的方式屏蔽、过滤广告信息。"),_c('br'),_vm._v("\n        7.3腾讯依照法律的规定对广告商履行相关义务，您应当自行判断广告信息的真实性并为自己的判断行为负责，除法律明确规定外，您因依该广告信息进行的交易或前述广告商提供的内容而遭受的损失或损害，腾讯不承担责任。 　　7.4您同意，对腾讯服务中出现的广告信息，您应审慎判断其真实性和可靠性，除法律明确规定外，您应对依该广告信息进行的交易负责。"),_c('br'),_vm._v("\n        八、【收费服务】"),_c('br'),_vm._v("8.1腾讯的部分服务是以收费方式提供的，如您使用收费服务，请遵守相关的协议。 "),_c('br'),_vm._v("8.2腾讯可能根据实际需要对收费服务的收费标准、方式进行修改和变更，腾讯也可能会对部分免费服务开始收费。前述修改、变更或开始收费前，腾讯将在相应服务页面进行通知或公告。如果您不同意上述修改、变更或付费内容，则应停止使用该服务"),_c('br'),_vm._v("\n        九、【第三方提供的产品或服务】 您在腾讯平台上使用第三方提供的产品或服务时，除遵守本协议约定外，还应遵守第三方的用户协议。腾讯和第三方对可能出现的纠纷在法律规定和约定的范围内各自承担责任。 　　十、【基于软件提供服务】\n        若腾讯依托“软件”向您提供服务，您还应遵守以下约定： 10.1您在使用本服务的过程中可能需要下载软件，对于这些软件，腾讯给予您一项个人的、不可转让及非排他性的许可。您仅可为访问或使用本服务的目的而使用这些软件。 10.2为了改善用户体验、保证服务的安全性及产品功能的一致性，腾讯可能会对软件进行更新。您应该将相关软件更新到最新版本，否则腾讯并不保证其能正常使用。\n        "),_c('br'),_vm._v("10.3腾讯可能为不同的终端设备开发不同的软件版本，您应当根据实际情况选择下载合适的版本进行安装。您可以直接从腾讯的网站上获取软件，也可以从得到腾讯授权的第三方获取。如果您从未经腾讯授权的第三方获取软件或与软件名称相同的安装程序，腾讯无法保证该软件能够正常使用，并对因此给您造成的损失不予负责。\n        "),_c('br'),_vm._v("10.4除非腾讯书面许可，您不得从事下列任一行为："),_c('br'),_vm._v("（1）删除软件及其副本上关于著作权的信息；"),_c('br'),_vm._v("（2）对软件进行反向工程、反向汇编、反向编译，或者以其他方式尝试发现软件的源代码； （3）对腾讯拥有知识产权的内容进行使用、出租、出借、复制、修改、链接、转载、汇编、发表、出版、建立镜像站点等；\n        "),_c('br'),_vm._v("（4）对软件或者软件运行过程中释放到任何终端内存中的数据、软件运行过程中客户端与服务器端的交互数据，以及软件运行所必需的系统数据，进行复制、修改、增加、删除、挂接运行或创作任何衍生作品，形式包括但不限于使用插件、外挂或非经腾讯授权的第三方工具/服务接入软件和相关系统；\n        "),_c('br'),_vm._v("（5）通过修改或伪造软件运行中的指令、数据，增加、删减、变动软件的功能或运行效果，或者将用于上述用途的软件、方法进行运营或向公众传播，无论这些行为是否为商业目的； （6）通过非腾讯开发、授权的第三方软件、插件、外挂、系统，登录或使用腾讯软件及服务，或制作、发布、传播非腾讯开发、授权的第三方软件、插件、外挂、系统。\n        "),_c('br'),_vm._v("十一、【知识产权声明】\n        "),_c('br'),_vm._v("11.1腾讯在本服务中提供的内容（包括但不限于网页、文字、图片、音频、视频、图表等）的知识产权归腾讯所有，用户在使用本服务中所产生的内容的知识产权归用户或相关权利人所有。 　　11.2除另有特别声明外，腾讯提供本服务时所依托软件的著作权、专利权及其他知识产权均归腾讯所有。\n        "),_c('br'),_vm._v("11.3腾讯在本服务中所使用的“QQ”、“腾讯”、“TENCENT”及企鹅形象等商业标识，其著作权或商标权归腾讯所有。\n        "),_c('br'),_vm._v("11.4上述及其他任何本服务包含的内容的知识产权均受到法律保护，未经腾讯、用户或相关权利人书面许可，任何人不得以任何形式进行使用或创造相关衍生作品。\n        "),_c('br'),_vm._v("十二、【用户违法行为】"),_c('br'),_vm._v("12.1您在使用本服务时须遵守法律法规，不得利用本服务从事违法违规行为，包括但不限于：\n        "),_c('br'),_vm._v("（1）发布、传送、传播、储存危害国家安全统一、破坏社会稳定、违反公序良俗、侮辱、诽谤、淫秽、暴力以及任何违反国家法律法规的内容；\n        "),_c('br'),_vm._v("（2）发布、传送、传播、储存侵害他人知识产权、商业秘密等合法权利的内容；\n        "),_c('br'),_vm._v("（3）恶意虚构事实、隐瞒真相以误导、欺骗他人；"),_c('br'),_vm._v("\n        （4）发布、传送、传播广告信息及垃圾信息；\n        "),_c('br'),_vm._v("（5）其他法律法规禁止的行为。\n        "),_c('br'),_vm._v("12.2如果您违反了本条约定，相关国家机关或机构可能会对您提起诉讼、罚款或采取其他制裁措施，并要求腾讯给予协助。造成损害的，您应依法予以赔偿，腾讯不承担任何责任。\n        　　12.3如果腾讯发现或收到他人举报您发布的信息违反本条约定，腾讯有权进行独立判断并采取技术手段予以删除、屏蔽或断开链接。同时，腾讯有权视用户的行为性质，采取包括但不限于暂停或终止服务，限制、冻结或终止QQ号码使用，追究法律责任等措施。\n        　　12.4您违反本条约定，导致任何第三方损害的，您应当独立承担责任；腾讯因此遭受损失的，您也应当一并赔偿。 　　十三、【遵守当地法律监管】 　　13.1您在使用本服务过程中应当遵守当地相关的法律法规，并尊重当地的道德和风俗习惯。如果您的行为违反了当地法律法规或道德风俗，您应当为此独立承担责任。\n        　　13.2您应避免因使用本服务而使腾讯卷入政治和公共事件，否则腾讯有权暂停或终止对您的服务。 　　十四、【用户发送、传播的内容与第三方投诉处理】 　　14.1您通过本服务发送或传播的内容（包括但不限于网页、文字、图片、音频、视频、图表等）均由您自行承担责任。\n        　　14.2您发送或传播的内容应有合法来源，相关内容为您所有或您已获得权利人的授权。 　　14.3您同意腾讯可为履行本协议或提供本服务的目的而使用您发送或传播的内容。 　　14.4如果腾讯收到权利人通知，主张您发送或传播的内容侵犯其相关权利的，您同意腾讯有权进行独立判断并采取删除、屏蔽或断开链接等措施。\n        　　14.5您使用本服务时不得违反国家法律法规、侵害他人合法权益。您理解并