import {mapGetters, mapMutations} from 'vuex'
import axios from 'axios'

export default {
  name: "head",
  props: ["currentTab"],
  data() {
    return {
      userinfo: {}
    }
  },
  mounted() {
    this.getProfile()
  },
  activated() {
    this.getProfile()
  },
  computed: {
    title() {
      return this.currentTab == 1 ? "消息" : this.currentTab == 2 ? "联系人" : "关于";
    },
    ...mapGetters(["login", "user"])
  },
  methods: {
    openSideMenu() {
      this.$emit("openSideMenu");
    },
    getProfile() {
      axios
        .get("/api/getprofile", {
          params: {
            qq: this.user.qq
          }
        })
        .then(response => {
          console.log(response);
          this.userinfo = response.data;
        })
        .catch(err => {
          console.log;
        });
    }
  }
};



