
import axios from "axios";
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
data() {
return {
formdata: {
qq: "",
password: "",
question: "",
answer: "",
nickname: "",
sex: "",
place: "",
signature: "",
age: ""
},
response: "",
alert: "",
showAlert: false,
showloading: false
};
},
methods: {
regist() {
this.showloading = true;
if (!this.validation()) {
this.showloading = false;
return;
}
axios.post("/api/doregist", this.formdata).then(response => {
this.response = response.data;
if (this.response == "-1") {
this.alert = "该QQ已被注册过";
this.showAlert = true;
this.showloading = false;
this.handleRes("QQ号已被注册");
return;
}
if (this.response == "-2") {
this.handleRes("服务器错误");
return;
}
this.handleRes("注册成功");
this.clearForm();
var that = this;
setTimeout(function() {
that.showAlert = false;
that.$router.push("/login");
}, 2000);
});
},
validation() {
var data = this.formdata;
if (data.qq.length < 6 && data.password.length < 6 && data.sex.length && data.question.length && data.answer.length && data.nickname.length<10 && data.nickname.length) {
this.alert = "必填字段没有完成，QQ与密码必须大于六位,昵称不能超过9位";
this.showAlert = true;
return false;
}
return true;
},
onHide() {
this.showAlert = false;
},
handleRes(alert) {
this.showloading = false;
this.alert = alert;
this.showAlert = true;
},
clearForm() {
this.formdata = {
qq: "",
password: "",
question: "",
answer: "",
nickname: "",
sex: "",
place: "",
signature: "",
age: ""
};
},
back(){
this.$router.go(-1)
}
},
components: {
Alert,
Loading
}
};



