
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapMutations } from "vuex";
import axios from "axios";
export default {
data() {
return {
qq: "",
pwd: "",
showloading: false,
showAlert: false,
response: "",
alert: ""
};
},
created() {
localStorage.user = ""
localStorage.login = ""
localStorage.weather = ""
},
components: {
Alert,
Loading
},
methods: {
login() {
this.showloading = true;
if (!this.validation(this.qq) || !this.validation(this.pwd)) {
this.handleRes("QQ号或密码不能为空");
return;
}
axios
.post("/api/dologin", { qq: this.qq, password: this.pwd })
.then(response => {
if (response.data == "-1") {
this.handleRes("QQ号错误");
return;
}
if (response.data == "-2") {
this.handleRes("密码错误");
return;
}
this.showloading = false;
this.setLogin(response.data);
this.getWeather();
this.clearForm();
this.$router.push("/message");
})
.catch(err => {
console.log(err);
});
},
onHide() {
this.showAlert = false;
},
validation(sth) {
if (!sth.length > 0) {
return false;
}
return true;
},
clearForm() {
this.response = "";
this.qq = "";
this.pwd = "";
this.alert = "";
},
handleRes(alert) {
this.showloading = false;
this.alert = alert;
this.showAlert = true;
},
...mapMutations({
setLogin: "SET_LOGIN",
setWeather: "SET_WEATHER"
}),
getWeather() {
this.setWeather();
}
},
computed: {}
};



