import VFooter from "pages/Common/Footer/Footer.vue";
import VHeader from "pages/Common/Header/Header.vue";
import Scroll from "base/Scroll/Scroll.vue";
import axios from "axios";
import {mapGetters} from "vuex";

export default {
  data() {
    return {
      userinfo: {
        avatar: "avatar.jpg",
        background: "background.jpg"
      },
      showPreview: false,
      scrollY: 0,
      height: 0,
      currentUser: {}
    };
  },
  components: {
    VFooter,
    VHeader,
    Scroll
  },
  created() {
    this.probeType = 3;
  },
  computed: {},
  mounted() {
    this.getProfile();
    this.height = this.$refs.background.clientHeight;
    this.currentUser = JSON.parse(localStorage.user);
  },
  activated() {
    this.getProfile();
    this.height = this.$refs.background.clientHeight;
    this.currentUser = JSON.parse(localStorage.user);
  }
  ,
  methods: {
    getProfile() {
      axios
        .get("/api/getprofile", {
          params: {
            qq: this.$route.params.qq
          }
        })
        .then(response => {
          this.userinfo = response.data;
        })
        .catch(err => {
          console.log;
        });
    },
    back() {
      this.$router.go(-1);
    },
    scroll(pos) {
      this.scrollY = pos.y;
    },
    toSetting() {
      if (this.userinfo.qq == this.currentUser.qq) {
        this.$router.push("/setting");
      } else {
        return
      }
    },
    toChat() {
      this.$router.push(`/chatone/${this.userinfo.qq}`)
    }
  },
  watch: {
    scrollY(newVal) {
      var scale = 1;
      const percent = 2 * Math.abs(newVal / this.height);
      scale = 1 + percent;
      if (newVal < 0) {
        this.$refs.background.style.transform = `translate3d(0,${newVal}px,0)`;
      } else {
        this.$refs.background.style.transform = `scale(${scale})`;
      }
    }
  }
};



