
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import axios from "axios";
export default {
data() {
return {
alert: "",
showAlert: false,
response: "",
qq: "",
answer: "",
newpwd: "",
showloading: false
};
},
methods: {
onHide() {
this.showAlert = false;
},
getQues() {
this.showloading = true;
if (!this.validation(this.qq)) {
this.handleRes("QQ号不能为空");
return;
}
axios
.get("/api/getques", {
params: {
qq: this.qq
}
})
.then(response => {
if (response.data == "-1") {
this.handleRes("QQ号不存在");
return;
}
if (response.data == "-2") {
this.handleRes("服务器错误");
return;
}
this.response = response;
this.showloading = false;
})
.catch(err => {
console.log(err);
});
},
resetpwd() {
this.showloading = true;
if (!this.validation(this.qq)) {
this.handleRes("QQ号不能为空");
return;
}
if (!this.validation(this.answer) || !this.validation(this.newpwd)) {
this.handleRes("密保答案或者新密码不能为空");
return;
}
axios
.post("/api/setnewpwd", {
qq: this.qq,
answer: this.answer,
newpwd: this.newpwd
})
.then(response => {
if (response.data == "-1") {
this.handleRes("密保问题回答错误");
return;
}
if (response.data == "-2") {
this.handleRes("服务器错误");
return;
}
this.handleRes("修改成功");
this.clearForm();
var that = this;
setTimeout(function() {
that.showAlert = false;
that.$router.push("/login");
}, 2000);
})
.catch(err => {
console.log(err);
});
},
validation(sth) {
if (!sth.length > 0) {
return false;
}
return true;
},
clearForm() {
this.response = "";
this.qq = "";
this.answer = "";
this.newpwd = "";
},
handleRes(alert) {
this.showloading = false;
this.alert = alert;
this.showAlert = true;
},
back(){
this.$router.go(-1)
}
},
components: {
Alert,
Loading
},
mounted(){

}
};



